button {
  font-family: "Roboto", sans-serif;
  outline: none;
}

.App {
  display: flex;
  flex-direction: column;
  position: relative;
}

.Navbar {
  display: flex;
  justify-content: space-between;
  padding: 0;
  background-color: rgba(238, 238, 238, 0.7);
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.5);
}

.Navbar-menu {
  display: flex;
  padding: 0;
  margin: 0;
}

.Navbar-menu li {
  list-style: none;
  list-style-type: none;
  padding: 0.5rem;
  -webkit-transition: background-color 300ms ease-in-out;
  transition: background-color 300ms ease-in-out;
  cursor: pointer;
}

#logo {
  padding: 0.2rem;
}

.Navbar-menu li:hover {
  background-color: rgba(238, 238, 238, 0.7);
}

.Navbar-menu button {
  border: none;
  background-color: transparent;
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.8);
  -webkit-transition: color 300ms ease-in-out;
  transition: color 300ms ease-in-out;
}

.Navbar-menu li:hover button {
  color: rgba(0, 0, 0, 0.6);
}

.Board {
  margin: auto;
  width: 95%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.board-title form {
  display: flex;
  justify-content: space-between;
  background-color: white;
  border: 2px solid rgb(132, 200, 255);
  width: 300px;
  border-radius: 5px;
}

.board-title input {
  font-size: 2rem;
  width: 250px;
  margin: 0;
  border: none;
  background-color: transparent;
  font-family: Roboto, sans-serif;
  font-weight: bold;
  letter-spacing: 0px;
  padding: 0.4rem;
}

.board-title input:focus {
  outline-width: 0;
}

.board-title button {
  background-color: rgba(128, 128, 128, 0.3);
  border: none;
  padding: 0.4rem;
}

.board-title h1 {
  font-size: 2rem;
  margin: 0;
  padding: 0.4rem;
  border: 2px solid transparent;
}

.Lane {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: 1px solid grey;
  width: 100%;
  margin-right: 0.5rem;
  padding: 0.5rem;
  background-color: rgba(215, 215, 215, 0.8);
  border-radius: 7px;
}

.Lane .task {
  background-color: rgba(255, 255, 255, 0.9);
  margin-bottom: 0.5em;
  padding: 0.5rem 1rem;
  border-radius: 3px;
  box-shadow: 1px 1px 2px 0px rgba(133, 133, 133, 1);
}

.Lane .task:hover {
  background-color: #ffffff;
  box-shadow: none;
}

span.add-item {
  /* background-color: #ffffff77; */
  opacity: 0.5;
  cursor: pointer;
  -webkit-transition: opacity 300ms ease-in-out;
  transition: opacity 300ms ease-in-out;
}

span.add-item:hover {
  opacity: 0.8;
}

.Lane h3 {
  margin: 0.5rem 0;
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
  border: 2px solid transparent;
  padding: 0.4rem 0;
}

.Lane form {
  display: flex;
  background-color: white;
  border: 2px solid rgb(132, 200, 255);
  width: 100%;
  border-radius: 3px;
  margin: 0.4rem 0;
}

.Lane form input {
  font-size: 1.2rem;
  flex-grow: 1;
  margin: 0.5rem 0;
  border: none;
  background-color: transparent;
  font-family: Roboto, sans-serif;
  font-weight: bold;
  padding: 0;
  width: 100%;
}

.Lane form input:focus {
  outline-width: 0;
}

.Lane form input::-ms-clear {
  display: none;
}

.Lane form button {
  background-color: rgba(128, 128, 128, 0.3);
  border: none;
  padding: 0.4rem;
}

.Lane.empty {
  opacity: 0.6;
  cursor: pointer;
  -webkit-transition: opacity 300ms ease-in-out;
  transition: opacity 300ms ease-in-out;
}

.Lane.empty h3 {
  justify-content: flex-start;
}

.Lane.empty span {
  margin-left: 10px;
}

.Lane.empty:hover {
  opacity: 1;
}

.lane-container {
  box-sizing: border-box;
  padding: 0 0.5rem;
  flex-basis: calc(100% / 6);
}

.add-item-text {
  margin-left: 10px;
}

.item-details {
  position: absolute;
  top: 0rem;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 5;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-form-container {
  background-color: rgb(219, 219, 219);
  z-index: 6;
  box-shadow: 2px 2px 15px 2px rgb(0, 0, 0, 0.75);
  max-width: 80%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem;
  border-radius: 0.3rem;
}

@media (min-width: 768px) {
  .item-form-container {
    max-width: 50%;
  }
}

.item-form-container form {
  display: flex;
  flex-direction: column;
}

.item-form-container textarea,
.item-form-container input {
  font-size: 1.2em;
  font-family: inherit;
  background-color: transparent;
  border: transparent;
  outline-color: transparent;
}

.label-logo {
  display: flex;
  margin-bottom: 1em;
}

.label-logo .logo {
  width: 30px;
  text-align: center;
}

.label-input {
  display: flex;
  flex-direction: column;
  font-size: 1.1rem;
  border: 2px solid rgb(132, 200, 255);
  width: 100%;
  border-radius: 3px;
  padding: 4px;
}

.current-lane {
  font-weight: 900;
}

.buttons {
  display: flex;
  justify-content: space-between;
  font-size: 1.1rem;
  width: 100%;
  padding: 0;
  padding-bottom: 4px;
}

.buttons input {
  border: 2px solid rgb(159, 193, 243);
  border-radius: 5px;
  padding: 4px;
  color: white;
  font-size: 0.9rem;
  background-color: rgb(79, 115, 148);
  -webkit-transition: background-color 200ms ease-in-out;
  transition: background-color 200ms ease-in-out;
}

.buttons input:hover,
.buttons input:disabled {
  background-color: rgb(153, 187, 238);
}

.buttons input:disabled {
  color: rgb(202, 202, 202);
}

.buttons input.delete-button {
  background-color: rgb(202, 74, 74);
  border-color: rgb(199, 118, 118);
}

.buttons input.delete-button:hover {
  background-color: rgb(202, 109, 109);
}

.label-input::-ms-clear {
  display: none;
}

.label-input label {
  margin-bottom: 0.2em;
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.7);
}

.label-input p {
  margin-top: 0;
  margin-bottom: 0.3em;
}

.label-input p select {
  width: 100%;
  font-size: 1em;
}

.alert {
  text-align: center;
  color: rgb(204, 46, 46);
}

.SwimLanes {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  flex-grow: 1;
  padding: 1rem;
  border-radius: 20px;
  background: rgba(50, 100, 50, 0.8);
}

